import { Injectable } from '@angular/core';
import User from './../models/User';

@Injectable()
export default class UserService {
    private currentlyFetchedFollowedUsers: User[];
    private currentlyFetchedSearchResultUsers: User[];
    
    constructor() {
        this.currentlyFetchedFollowedUsers = this.createDummyData();
        this.currentlyFetchedSearchResultUsers = this.createDummyData();
    }
    private createDummyData(){
        return [
            new User(1, "Marko", "Mati", "https://image.freepik.com/free-icon/user-male-shape-in-a-circle-ios-7-interface-symbol_318-35357.jpg", true),
            new User(2, "Ante", "Anti", "https://upload.wikimedia.org/wikipedia/commons/d/d3/User_Circle.png", false),
            new User(3, "Luka", "Luki", "http://www.ccusersforum.org/images/user.png", false),
            new User(4, "Toni", "Marti", "http://icons.iconarchive.com/icons/hopstarter/scrap/256/User-icon.png", true),
            new User(5, "Ivan", "Ivi", "http://icons.iconarchive.com/icons/double-j-design/origami-colored-pencil/256/blue-user-icon.png", true),
            new User(6, "Jure", "Juri", "https://image.freepik.com/free-icon/user-male-shape-in-a-circle-ios-7-interface-symbol_318-39025.jpg", false),
            new User(7, "Dino", "Dinosauri", "http://downloadicons.net/sites/default/files/user-icon-45917.png", true),
            new User(8, "Ivana", "Ivankovi", "https://upload.wikimedia.org/wikipedia/en/thumb/c/ce/User-info.svg/1024px-User-info.svg.png", false),
            new User(9, "Martina", "Martini", "http://colorvisiontesting.com/images/plate%20with%205.jpg", true),
            new User(10, "Anamaria", "Abecei", "https://www.sencha.com/wp-content/uploads/2016/02/icon-sencha-test-studio.png", true)
        ];
    }
    public getFolowedUsers(){
        //retrieve users from server that current user follows
        this.currentlyFetchedFollowedUsers = this.createDummyData();
        return this.currentlyFetchedFollowedUsers;
    } 
    public getUsersBySearchString(searchString: string){
        //retrieve users from server that have searchString in fullname
        this.currentlyFetchedSearchResultUsers = this.createDummyData();
        return this.currentlyFetchedSearchResultUsers;
    }
    public getUserById(id: number){
        //get user from server
        return this.currentlyFetchedSearchResultUsers.find(x => x.id == id);
    }
    public getCurrentUser(){
        //get user of this session from server
        return new User(1, "Test", "User", "http://www.wikiality.com/file/2016/11/bears1.jpg", null);
    }
    public getCurrentUserEMail(){
        return "test@example.com";
    }
    public followUser(user: User){
        // Follow user on server
        user.doesCurrentUserFollowThisUser = true;
    }
    public unfollowUser(user: User){
        // Unfollow user on server
        user.doesCurrentUserFollowThisUser = false;
    }
}


